export class Matches {
    // home team
    HomeAbbreviation: string[];
    HomePoints: number[];
    // visitor team
    VisitorAbbreviation: string[];
    VisitorPoints: number[];
    // game status
    status: string[];
    livePcTime: string[];
    // points
    HomeQts: number[];
    HomeOts: number[];
    VisitorQts: number[];
    VisitorOts: number[];
}



